.style-black{
  background: #000000;
  color: white;
}

.page-body{
  padding-bottom: 70px;
}

.swiper-item{
  display: block;
  height: 200px;
  width: 100%;
  background: powderblue;
}

.scroll-view_H{
  white-space: nowrap;
  width: 100%;
}

.page-section{
  .scroll-view_H{
    border-bottom: 10px solid #f1f1f1;
  }
}

.page-section-title{
  padding:17px 15px 0;
  display: block;
  > view{
    padding-top: 12px;
    overflow: hidden;
  }
  .style-series-cell{
    width: 20.8%; height: 28px; float: left;margin: 0 1.33% 10px;
    text-align: center; line-height: 28px; font-size: 12px; border: 2px solid #000000; border-radius: 16px;
  }
}

.scroll-view-item_H{
  display: inline-block;
  padding: 12px 0 15px 15px ;
  width: 50%;
}
.artifact_name{
  font-size: 16px;
}
.artifact_teacher{
  margin: 2px 0 4px;
  font-size: 12px;
  color: #676767;
}
.artifact_info{
  font-size: 12px;
  color: #676767;
}

.scroll-view-item-image{
  width: 100%;
  height: 222px;
}

.style-series-waterfall{
  margin: 0 14px;
}
/* 单个图片容器的样式 */
.img_item {
  width:  calc(~'50% - 7px');
  display: inline-block;
  vertical-align: top;
  >view{
    margin-bottom: 24px;
  }
}
.img_item:last-child {
  margin-left: 14px;
}



.add-more-artifact{
  height: 44px;
  line-height: 44px;
  border: 2px solid #000000;
  font-size: 16px;
  text-align: center;
}

.page-artict, .artifact-classify{
  .page-section-title{
    border-top: 10px solid #f1f1f1;
    display: flex; justify-content: space-between; align-items: center;
    image{
      width: 24px; height: 24px;
    }
  }
}
.page-artict{
  >view:nth-child(2){
    padding-bottom: 20px;
  }
}

.artict-cell{
  margin: 20px 15px 0; box-shadow: 0 3px 15px  #888888;
  padding: 20px;
  .artist-info{
    display: flex;justify-content: space-between; align-items: center;

    .avatar{
      width: 60px; height: 60px; border-radius: 50%;margin-right: 10px;
    }
    >view{
      display: flex;justify-content: flex-start;align-items: center;
    }
    .artist-name {
      font-size: 18px;  font-weight: bolder; padding-bottom: 3px;
    }
    .artist-artifact{
      font-size: 14px;
    }
    >button{
       width: 60px; height: 30px; margin: 0; text-align: center; line-height: 26px; font-size: 14px; border: 2px solid #000000;border-radius: 0;
    }
    >button::after{
      border-radius: 0;
    }
  }
  .artist-artifact{
    >image{
      width: 31%; height: 116px; margin: 20px 1.16% 0;
    }
  }
}

.artifact-classify{
  .artifact-classify-scroll-view{
    position: relative;
    display: inline-block;
    margin: 12px 0 15px 15px ;
    width: 48%;

    .artifact-classify-item-image{
      height: 107px; width: 100%;
    }
    .artifact-classify-name{
      position: absolute;
      z-index: 9;
      color: #ffffff;
      font-size: 14px;
      top:0;
      left: 0;
      right: 0;
      bottom: 5px;
      background-color: rgba(0, 0, 0, 0.1);
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

